.table > tbody > tr > td {
	vertical-align: top;  /* Overruling Bootstrap for checkboxes and radios, since Bootstrap align them at top. */
}


.checkbox-item{
	label {
    padding-left: 15px;
	}
	
	.ls-label-xs-visibility{
		width: 27px;
		height: 27px;
		
		&::after{
		width: 25px;
		height: 25px;
		border-radius: 2px;
		padding-top:4px;
		padding-left: 1px;
		}
	}
	
	input[type="checkbox"]:checked + label::after, 
	input[type="radio"]:checked + label::after {
		color: #fff;
		width: 25px;
		padding-left: 3px;
	}
}

.checkbox-list,
.checkbox-item{
	label::after{
		width: 25px;
		height: 25px;
		background-color: $color1;
		font-size: 20px;
		padding-right: 2px;
		padding-top: 0;
		border-radius: 2px;
		
	}
	
	label::before{
		width: 27px;
		height: 27px;
	}
}

label::after{
	background-color: $color1;
}

.radio-item label::after{
	background-color: $color1;
}

.completed-wrapper{  /* development only */
	.assessment-table{
		display: none;
	}
}

.panel-primary{
	border-color: $color1;
	
	.panel-heading{
		background: $color1;
		border-color: $color1;
		
		h4{
			color: white;
		}
	}
}